/* src/pages/BlogLibrary.css */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

.blog-library-page {
  width: 100%;
  background-color: #EDEBE8;
}

.blog-library-content {
  padding: 60px 0;
  background-color: #EDEBE8;
}

.blog-library-page .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  box-sizing: border-box;
}

.blog-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 60px;
  text-align: center;
}

.blog-header h1 {
  font-size: 36px;
  font-weight: 700;
  color: #173A37;
  margin-bottom: 10px;
  font-family: 'Playfair Display', serif;
}

.blog-header h2 {
  font-size: 24px;
  font-weight: 400;
  color: #173A37;
  margin-bottom: 15px;
  font-family: 'Playfair Display', serif;
}

.blog-body {
  max-width: 1200px;
  margin: 0 auto;
}

.blog-content {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
}

.blog-posts {
  flex: 3;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
}

.blog-post {
  background: #fff;
  border-radius: 20px;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.blog-post:hover {
  transform: translateY(-5px);
}

.post-image img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.post-content {
  padding: 20px;
}

.post-meta {
  display: flex;
  gap: 15px;
  margin-bottom: 10px;
  font-size: 14px;
  color: #888;
  font-family: 'Playfair Display', serif;
}

.post-meta .date {
  color: #FF6F61;
}

.post-meta .category {
  color: #173A37;
}

.post-title {
  font-size: 24px;
  font-weight: 700;
  font-family: 'Playfair Display', serif;
  margin-bottom: 10px;
  color: #173A37;
}

.post-title-link {
  color: #173A37;
  text-decoration: none;
}

.post-title-link:hover {
  color: #FF6F61;
  text-decoration: underline;
}

.post-excerpt {
  font-size: 16px;
  line-height: 1.6;
  color: #173A37;
  font-family: 'Playfair Display', serif;
  margin-bottom: 15px;
}

.blog-sidebar {
  flex: 1;
  min-width: 250px;
}

.sidebar-widget {
  margin-bottom: 30px;
}

.sidebar-widget h3 {
  font-size: 28px;
  font-weight: 700;
  color: #173A37;
  margin-bottom: 20px;
  font-family: 'Playfair Display', serif;
}

.sidebar-search .search-form {
  display: flex;
  align-items: center;
  gap: 10px;
}

.sidebar-search .search-form input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-family: 'Playfair Display', serif;
  font-size: 14px;
}

.sidebar-search .search-form button {
  background: #FF6F61;
  border: none;
  padding: 10px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.sidebar-search .search-form button i {
  font-size: 16px;
}

.sidebar-search .search-form button:hover {
  background: #173A37;
}

.sidebar-categories ul,
.sidebar-recent-posts ul {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.sidebar-categories ul li,
.sidebar-recent-posts ul li {
  font-size: 16px;
  line-height: 1.6;
  color: #173A37;
  font-family: 'Playfair Display', serif;
  margin-bottom: 10px;
}

.sidebar-recent-posts ul li p {
  font-size: 16px;
  line-height: 1.6;
  color: #173A37;
  font-family: 'Playfair Display', serif;
  margin-bottom: 0;
}

.blog-pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 40px;
}

.blog-pagination .page-btn {
  background: #fff;
  border: 1px solid #ddd;
  padding: 12px 15px;
  border-radius: 20px;
  font-family: 'Playfair Display', serif;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
}

.blog-pagination .page-btn.active {
  background: #FF6F61;
  color: white;
  border-color: #FF6F61;
}

.blog-pagination .page-btn:hover {
  background: #173A37;
  color: white;
  border-color: #173A37;
}

/* Media Queries για Κινητά */
@media (max-width: 768px) {
  .blog-library-content {
    padding: 40px 0;
  }

  .blog-library-page .container {
    padding: 0 20px;
  }

  .blog-header {
    gap: 8px;
    margin-bottom: 40px;
  }

  .blog-header h1 {
    font-size: 28px;
    margin-bottom: 8px;
  }

  .blog-header h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }

  .blog-content {
    flex-direction: column;
  }

  .blog-posts {
    grid-template-columns: 1fr;
  }

  .blog-post {
    max-width: 100%;
  }

  .post-image img {
    height: 180px;
  }

  .post-content {
    padding: 15px;
  }

  .post-meta {
    font-size: 13px;
  }

  .post-title {
    font-size: 20px;
  }

  .post-excerpt {
    font-size: 14px;
    margin-bottom: 10px;
  }

  .sidebar-widget {
    margin-bottom: 20px;
  }

  .sidebar-widget h3 {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .sidebar-search .search-form input {
    padding: 8px;
  }

  .sidebar-search .search-form button {
    padding: 8px;
  }

  .sidebar-categories ul li,
  .sidebar-recent-posts ul li {
    font-size: 14px;
    margin-bottom: 8px;
  }

  .sidebar-recent-posts ul li p {
    font-size: 14px;
  }

  .blog-pagination .page-btn {
    padding: 10px 12px;
    font-size: 12px;
  }
}

@media (max-width: 576px) {
  .blog-library-content {
    padding: 30px 0;
  }

  .blog-library-page .container {
    padding: 0 10px;
  }

  .blog-header {
    gap: 6px;
    margin-bottom: 30px;
  }

  .blog-header h1 {
    font-size: 24px;
    margin-bottom: 6px;
  }

  .blog-header h2 {
    font-size: 18px;
    margin-bottom: 8px;
  }

  .post-image img {
    height: 160px;
  }

  .post-meta {
    font-size: 12px;
  }

  .post-title {
    font-size: 18px;
  }

  .post-excerpt {
    font-size: 13px;
    margin-bottom: 8px;
  }

  .sidebar-widget h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }

  .sidebar-categories ul li,
  .sidebar-recent-posts ul li {
    font-size: 13px;
    margin-bottom: 6px;
  }

  .sidebar-recent-posts ul li p {
    font-size: 13px;
  }

  .blog-pagination .page-btn {
    padding: 8px 10px;
    font-size: 10px;
  }
}

@media (max-width: 375px) {
  .blog-library-content {
    padding: 20px 0;
  }

  .blog-library-page .container {
    padding: 0 5px;
  }

  .blog-header {
    gap: 5px;
    margin-bottom: 20px;
  }

  .blog-header h1 {
    font-size: 20px;
    margin-bottom: 5px;
  }

  .blog-header h2 {
    font-size: 16px;
    margin-bottom: 6px;
  }

  .post-image img {
    height: 140px;
  }

  .post-meta {
    font-size: 11px;
  }

  .post-title {
    font-size: 16px;
  }

  .post-excerpt {
    font-size: 12px;
    margin-bottom: 5px;
  }

  .sidebar-widget h3 {
    font-size: 18px;
    margin-bottom: 8px;
  }

  .sidebar-categories ul li,
  .sidebar-recent-posts ul li {
    font-size: 12px;
    margin-bottom: 5px;
  }

  .sidebar-recent-posts ul li p {
    font-size: 12px;
  }

  .blog-pagination .page-btn {
    padding: 6px 8px;
    font-size: 9px;
  }
}